type Props = {
    variant: 'red' | 'green' | 'blue',
    size?: 'sm' | 'md' | 'lg',
    label: string
}
const Button = ({variant = 'blue', size = 'md', label = 'Button'}: Props) => {
    const getSize = (size: string) => {
        switch (size) {
            case 'sm':
                return 'small'
            case 'md':
                return 'medium'
            case 'lg':
                return 'large'
            default:
                return 'medium'
        }
    }
    return (
        <button style={{ color: 'white', border: 'none',borderRadius: 10,padding: 10, background: variant, fontSize: getSize(size) }}>
            {label}
        </button>
    )
}

export default Button